    import headTop from '@/components/base/header/head'
    export default {
        components: {
            headTop
        },
        data() {
            return {
                info: "",   //  初始数据
                showtime: false,   // 显示自定义事件弹窗
                startTime: "",  //  开始时间
                entTime: "",  //  结束时间
                start: null,
                end: null,
                value: 0,   //  默认显示昨天
                percent: 66,  // 对于图形的具体填充数值
            }
        },
        created(){
            this.startTime = this.common.commonFun.get_date(1)
            this.entTime = this.common.commonFun.get_date(1)
            this.getInfo();
        },
        methods:{
            getInfo(){
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.workTimeRatio,
                    type: "post",
                    data: {
                        start: this.startTime,
                        end: this.entTime,
                    },
                    success: (response, allResponse) => {
                        // console.log('response',response)
                        this.info = response
                    }
                });
            },
            dates(){
                this.showtime = true
            },
            //            自定义弹窗=>点击确定
            onConfirm(){
                this.start = this.startTime.replace(/\-/g,'.').substring(2,10);
                this.end = this.entTime.replace(/\-/g,'.').substring(2,10);
                this.getInfo()
            },
//            选择时间
            date(){
                this.startTime = this.common.commonFun.get_date(1)
                this.entTime = this.common.commonFun.get_date(1)
                this.getInfo()
            }
        }
    }



